/**
 * @description undo menu
 * @author wangfupeng
 */

import {
  DomEditor, IButtonMenu, IDomEditor, t,
} from '@wangeditor-next/core'
import { Editor, Transforms } from 'slate'

import { CODE_MODE } from '../../../constants/icon-svg'

class SourceCode implements IButtonMenu {
  title = t('sourceCode.title')

  iconSvg = CODE_MODE

  tag = 'button'

  active = false

  getValue(_editor: IDomEditor): string | boolean {
    return _editor.getHtml()
  }

  isActive(_editor: IDomEditor): boolean {
    return this.active
  }

  isDisabled(editor: IDomEditor): boolean {
    if (editor.selection == null) { return true }
    return false
  }

  exec(editor: IDomEditor, _value: string | boolean) {
    this.active = !this.active
    this.sourceCodeMode(this.active, editor)
  }

  private sourceCodeMode(active: Boolean, editor: IDomEditor) {
    const html = editor.getHtml()

    editor.clear()

    // å é¤è¡¨æ ¼
    const tableEntries = Array.from(
      Editor.nodes(editor, {
        match: n => DomEditor.getNodeType(n) === 'table',
        universal: true,
      }),
    )

    if (Array.from(tableEntries).length > 0) {
      Transforms.removeNodes(editor, { mode: 'highest' })
    }

    // æå¥ä»£ç å
    if (active) {
      editor.dangerouslyInsertHtml(this.parseHtmlToCode(html))
    } else {
      editor.dangerouslyInsertHtml(this.parseCodeToHtml(html))
      editor.deleteForward('line')
    }
  }

  private parseHtmlToCode(html: string) {
    const escapedHtml = html
      .replace(/(<\/[^>]+>)(?!\s*$)/g, '$1\n')
      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')

    return `<pre><code class="language-html">${escapedHtml}</code></pre>`
  }

  private parseCodeToHtml(escapedHtml: string) {
    const REGEX = /<pre><code class="language-html">([\s\S]*?)<\/code><\/pre>/gi

    const match = REGEX.exec(escapedHtml)

    if (match?.[0] && match?.[1]) {
      return match[1]
        .replace(/&lt;/g, '<')
        .replace(/&gt;/g, '>')
        .replace(/<p><br><\/p>/g, '')
        .trim()
    }

    return ''
  }
}

export default SourceCode
